@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-servicios{

    display: grid;
    gap: 2rem;

    @include m.tablet{
        @include m.grid(2, 2rem);
    }
    .servicio{

        background-color: v.$blanco;
        padding: 3rem;
        border-radius: 1rem;
        transition: transform 300ms ease;
        text-align: center;

        &.seleccionado{
            background-color: v.$azul;
            p{
                color: v.$blanco;
            }
        }

        &:hover{
            cursor: pointer;
            transform: scale(1.05);
            transition: transform 300ms ease;
        }

        p{
            color: v.$negro;
            margin: 0 0 calc(v.$separacion / 4) 0 ;
        }

        .precio-servicio{
            color: v.$azul;
            font-weight: v.$bold;
            font-size: 3rem;
            margin: 0;
        }

    }
}